.banner-container{width:80%;padding:0 10%;height:540px;background:#000 url(/static/media/header.3b72e86b.png) left top no-repeat;background-size:cover;display:-webkit-flex;display:flex;text-align:left;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}@media only screen and (max-width: 600px){.banner-container{height:200px}}@media (min-width: 768px) and (max-width: 1023px){.banner-container{height:300px;width:90%;padding:0 5%}}.banner-container h1,.banner-container p,.banner-container button{color:#fff;display:-webkit-flex;display:flex;font-family:'Poppins', sans-serif;-webkit-align-self:flex-start;align-self:flex-start;font-weight:500;margin-bottom:10px}.banner-container h1{font-size:48px}@media (min-width: 768px) and (max-width: 1023px){.banner-container h1{font-size:35px}}@media only screen and (max-width: 600px){.banner-container h1{font-size:25px}}.banner-container p{font-size:20px}@media (min-width: 768px) and (max-width: 1023px){.banner-container p{font-size:16px}}@media only screen and (max-width: 600px){.banner-container p{font-size:12px}}

.button-container{background:transparent linear-gradient(90deg, #266efd 0%, #3876f1 100%) 0% 0% no-repeat padding-box;box-shadow:0px 3px 17px #3876f194;border-radius:5px;border:0;font-size:20px;font-family:'Poppins', sans-serif;padding:8px 31px;cursor:pointer;transition:all ease-in 0.3s}@media (min-width: 768px) and (max-width: 1023px){.button-container{font-size:16px;padding:7px 15px}}@media only screen and (max-width: 600px){.button-container{font-size:12px;padding:5px 12px}}.button-container:hover,.button-container:focus{box-shadow:0px 3px 17px #000}.button-container:active{opacity:0.75}

.post-container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin-left:35px;padding-left:35px;border-left:1px solid #aaa;font-family:'Manrope', sans-serif;font-size:16px;font-weight:300;line-height:24px;-webkit-flex-grow:1;flex-grow:1;-webkit-flex-basis:0;flex-basis:0;position:relative}@media only screen and (max-width: 600px){.post-container{font-size:14px}}.post-container:nth-child(1){margin-left:0;padding-left:0;border-left:0}@media only screen and (max-width: 600px){.post-container{border-left:0;margin-bottom:17.5px;padding-bottom:17.5px;border-bottom:1px solid #aaa;padding-left:0;margin-left:0}}.post-container .post-header{display:-webkit-flex;display:flex;width:100%;padding-top:5px}.post-container .post-header .ml-auto{margin-left:auto}.post-container .post-header .post-title{color:#276ffb;font-size:24px;font-weight:800;letter-spacing:0px;padding-bottom:10px}@media (min-width: 768px) and (max-width: 1023px){.post-container .post-header .post-title{font-size:18px}}@media only screen and (max-width: 600px){.post-container .post-header .post-title{font-size:16px}}.post-container .country-text{padding-left:10px}.post-container .text-light{color:rgba(20,20,20,0.6)}.post-container .post-desc{display:-webkit-flex;display:flex;width:100%;padding:20px 0}.post-container .post-details{display:-webkit-inline-flex;display:inline-flex;position:absolute;right:0;bottom:0;padding:8px 12px}.post-container .post-details svg{color:#fff}@media only screen and (max-width: 600px){.post-container .post-details{bottom:15px}}.post-container .post-details:hover,.post-container .post-details:focus{box-shadow:0px 3px 17px rgba(20,20,20,0.6)}

.sub-section-container{width:100%;display:-webkit-flex;display:flex;justify-items:center;margin-bottom:35px;-webkit-align-items:center;align-items:center}@media (min-width: 768px) and (max-width: 1023px){.sub-section-container{margin-bottom:11.66667px}}@media only screen and (max-width: 600px){.sub-section-container{margin-bottom:17.5px}}.sub-section-container .header{color:#141414;font-size:40px;font-family:'Poppins', sans-serif;font-weight:600}@media (min-width: 768px) and (max-width: 1023px){.sub-section-container .header{font-size:25px}}@media only screen and (max-width: 600px){.sub-section-container .header{font-size:18px}}.sub-section-container .child{font-family:'Poppins', sans-serif;margin-left:auto}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}.app-wrapper .mt-10{margin-top:10px}.app-wrapper .banner{margin-bottom:70px}@media only screen and (max-width: 600px){.app-wrapper .banner{margin-bottom:35px}}@media (min-width: 768px) and (max-width: 1023px){.app-wrapper .banner{margin-bottom:35px}}.app-wrapper .app-container{width:80%;margin:0 10%}@media (min-width: 768px) and (max-width: 1023px){.app-wrapper .app-container{width:90%;margin:0 5%}}.app-wrapper .app-container .section-container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:100%;margin-bottom:70px}@media only screen and (max-width: 600px){.app-wrapper .app-container .section-container{margin-bottom:35px}}@media (min-width: 768px) and (max-width: 1023px){.app-wrapper .app-container .section-container{margin-bottom:35px}}.app-wrapper .app-container .posts-container{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row}@media only screen and (max-width: 600px){.app-wrapper .app-container .posts-container{-webkit-flex-direction:column;flex-direction:column}}.app-wrapper .app-container .company-list-container{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-flex-wrap:wrap;flex-wrap:wrap}@media only screen and (max-width: 600px){.app-wrapper .app-container .company-list-container{-webkit-flex-wrap:wrap;flex-wrap:wrap}}@media (min-width: 768px) and (max-width: 1023px){.app-wrapper .app-container .company-list-container{-webkit-flex-wrap:wrap;flex-wrap:wrap}}.app-wrapper .app-container .view-all{display:-webkit-flex;display:flex;color:#276ffb;font-weight:800;cursor:pointer}@media only screen and (max-width: 600px){.app-wrapper .app-container .view-all{display:none}}.app-wrapper .app-container .view-all svg{margin-left:5px;transition:all ease-in 0.2s}.app-wrapper .app-container .view-all:hover,.app-wrapper .app-container .view-all:focus{opacity:0.8}.app-wrapper .app-container .view-all:hover svg,.app-wrapper .app-container .view-all:focus svg{-webkit-transform:translateX(5px);transform:translateX(5px)}

.company-container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;font-family:'Manrope', sans-serif;font-size:16px;font-weight:300;line-height:24px;width:140px;max-width:140px;height:130px;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;margin-right:8px}.company-container:last-child{margin-right:0}@media only screen and (max-width: 600px){.company-container{margin-right:0;margin-bottom:15px}}@media (min-width: 768px) and (max-width: 1023px){.company-container{margin-right:0;margin-bottom:15px}}.company-container .icon-container{width:90px;height:90px;border:1px solid rgba(20,20,20,0.6);border-radius:90px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;margin-bottom:10px;transition:all ease-in 0.3s}.company-container .icon-container svg{transition:all ease-in 0.3s}.company-container .name-container{font-size:20px;font-weight:800;transition:all ease-in 0.3s}@media only screen and (max-width: 600px){.company-container .name-container{font-size:14px}}@media (min-width: 768px) and (max-width: 1023px){.company-container .name-container{font-size:18px}}.company-container:hover .icon-container,.company-container:focus .icon-container{border-color:#276ffb}.company-container:hover .icon-container svg,.company-container:focus .icon-container svg{color:#276ffb}.company-container:hover .name-container,.company-container:focus .name-container{color:#276ffb}

